import { ChangeDetectorRef, Component, OnInit, ViewContainerRef, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { ToastController, ModalController, LoadingController } from '@ionic/angular';
import { DenominationSlideComponent } from 'src/app/components/denomination-slide/denomination-slide.component';
import { ApiService } from 'src/app/services/api.service';
import { DataService } from 'src/app/services/data.service';
import { LoadingService } from 'src/app/services/loading.service';
import { ToastService } from 'src/app/services/toast.service';

@Component({
  selector: 'app-cash-withdraw-v2',
  templateUrl: './cash-withdraw-v2.page.html',
  styleUrls: ['./cash-withdraw-v2.page.scss'],
})
export class CashWithdrawV2Page implements OnInit {
  public form: FormGroup;
  depositType: any;
  homeIconToggle: boolean = false;
  currentBalance: any;
  transAmt: any;
  animationState = 'out';
  

  repFG: FormGroup = this.formBuilder.group({
    personName: ['', Validators.required],
    phoneNo: ['', [Validators.required, Validators.minLength(10),
    Validators.maxLength(10)]],
    repdocumentId: [''],
    repdocumentNumber: [''],
  });
  frmToggle: boolean;
  ftmToggle: boolean;
  @ViewChild('dynamic', { read: ViewContainerRef }) private viewRef: ViewContainerRef;
  currencies: any;
  selectedCountryCodeForexTrans: string = '';


  constructor(private router: Router,
    private formBuilder: FormBuilder,
    private api: ApiService,
    public toastCtrl: ToastController,
    public toastService: ToastService,
    private modalController: ModalController,
    public loading: LoadingService,
    private shareDataService: DataService,
    private loadingController: LoadingController,
    private cdr: ChangeDetectorRef) { }

  ngOnInit() {
    this.formInit();

    this.currencies = JSON.parse(localStorage.getItem('COUNTRIES'));
  }

  formInit() {
    this.form = this.formBuilder.group({
      transactionId: [''],
      customerId: ['', [Validators.required]],
      productCode: ['FTX', [Validators.required]],
      tokenOrigin: ['Mobile', [Validators.required]],
      forexTransType: ['sale', [Validators.required]],
      forexCurrency: ['', [Validators.required]],
      forexAmount: ['', [Validators.required]],
      exchangeRate: ['', Validators.required],
      branchFlag: [true, [Validators.required]],
      accountBranch: ['', [Validators.required]],
      transactionCurrency: ['', [Validators.required]],
      transactionDate: ['', [Validators.required]],
      transactionBranch: ['', [Validators.required]],
      isMobileTrans: [true, [Validators.required]],
      transactionTime: ['', [Validators.required]],
      accountAmount: ['', [Validators.required]],
      totalChargeAmount: ['0', [Validators.required]],
      documentId: ['', [Validators.required]],
      documentNumber: ['', [Validators.required]],
      depositType: ['', [Validators.required]],
      forexTransCurrency: ['', [Validators.required]],
      forexTransAmount: ['', [Validators.required]],
      totalTransactionAmount: ['0', [Validators.required]],
      remarks: ['', [Validators.required]],
      // Audit log keys not required 
      totalAmount: [''],
      createdBy: [''],
      createdTime: [''],
      modifiedBy: [''],
      modifiedTime: [''],
      recordStatus: [''],
      authStatus: [''],
      version: [''],
    })
  }


  goBack() {
    this.router.navigate(['dashboard']);
  }

  segmentChanged(e: any) {
    if (e == 'representative') {
      this.form.addControl('representativeInfo', this.repFG);
    } else {
      this.form.removeControl('representativeInfo');
    }
    console.log(this.form.value);
  }

  numberOnlyValidation(event: any) {
    this.transAmt = event.target.value;
    this.transAmt = this.transAmt.toString().replace(/,/g, '');
    if (parseFloat(this.currentBalance) < parseFloat(this.transAmt)) {
      this.showToast();
    } else {
      return;
    }
  }

  keyPressNumbers(event) {
    var charCode = (event.which) ? event.which : event.keyCode;
    // Only Numbers 0-9
    if ((charCode < 48 || charCode > 57)) {
      event.preventDefault();
      return false;
    } else {
      return true;
    }
  }

  async showToast() {
    const toast = await this.toastCtrl.create({
      message: 'Transaction Amount should not exceed than Account Balance',
      duration: 2000,
      position: 'bottom'
    });
    toast.present();
  }

  enableDisableAction(type: any) {
    switch (type) {
      case 'FRM':
        this.toggleFrm();
        break;
      default:
        console.log("No such type exists!");
        break;
    }
  }

  toggleFrm() {
    this.frmToggle = !this.frmToggle;
    this.animationState = this.animationState === 'out' ? 'in' : 'out';
    this.createComponent();
  }

  createComponent() {
    this.viewRef.clear();
    let childComponentRef = this.viewRef.createComponent(DenominationSlideComponent);
    let childComponent = childComponentRef.instance;
    childComponent.unique_key = "viewRef";
    // childComponent.parentRef = this;
  }

  selectCurrencyCode(e: any) {
    console.log(e);
    if (typeof e !== "undefined") {
      const filteredCurrency = this.currencies?.filter((i: any) => i.currencyCode === e);
      console.log({filteredCurrency});
      
      this.selectedCountryCodeForexTrans = filteredCurrency[0].countryCode.toLowerCase();
      this.cdr.markForCheck();
    } else {
      return;
    }
  }
}
